.about-content {
	margin: 53px 0;

	.block-title {
		margin-bottom: 35px;
	}

	.image {
		height: 467px;
	}

	.description {
		overflow-y: auto;
	}


	&.about-3 {
		.image {
			height: 280px;
		}
	}

	@media (max-width: 1024.98px) {
		margin-top: 50px;

		.block-title {
			margin-bottom: 25px;
		}

		.image {
			height: 250px;
		}

		&.about-3 {
			.image {
				height: 250px;
			}
		}
	}

	@media (max-width: 767.98px) {
		margin-top: 25px;

		.container {
			.row {
				.col-md-6 {
					&:first-child {
						order: 2;
					}

					&:last-child {
						order: 1;
					}
				}
			}
		}

		.block-title {
			margin-bottom: 20px;
		}

		.image {
			margin-bottom: 15px;
		}
	}
}